import { useState, useEffect, useRef } from "react";
import { Box, VStack, Button, Text, HStack, Image } from "@chakra-ui/react";
import { useColorMode } from "@chakra-ui/react";
import { ExternalLinkIcon, CopyIcon } from "@chakra-ui/icons";
import Identicon from "./Identicon";

const WHITELIST_ADDRESS = process.env.REACT_APP_WHITELIST_ADDRESS;
const WHITELIST_ABI = require("../assets/wl-abi.json");

function NavLink({ children, ...props }: any) {
  const { colorMode } = useColorMode();
  return (
    <Box
      bg={colorMode === "dark" ? "gray.700" : "gray.100"}
      w="100%"
      p={2}
      mt={"3px"}
      border="3px"
      borderStyle="solid"
      borderRadius="5px"
      borderColor={colorMode === "dark" ? "gray.700" : "gray.100"}
    >
      {children}
    </Box>
  );
}

export default function TokenList(props: any) {
  const web3 = props.web3;
  // const account = props.account;
  const { colorMode } = useColorMode();
  var whitelistContract = new web3.eth.Contract(
    WHITELIST_ABI,
    WHITELIST_ADDRESS
  );
  const [tokens, setTokens] = useState<any>([]);
  // const [loading, setLoading] = useState<any>(false);
  const [copied, setCopied] = useState<any>(false);
  var isMounted = useRef(false);

  useEffect(() => {
    isMounted.current = true;
    whitelistContract.methods
      .getVaults("0x0b8D384b63a5e2428F649a43fE8B93627BE45cC4")
      .call()
      .then((tokens: any) => {
        if (isMounted.current && props?.account?.length) {
          setTokens(tokens);
          console.log(tokens);
        }
      })
      .catch((e: any) => {
        console.log(e);
      });
    return () => {
      // executed when unmount
      isMounted.current = false;
    };
  }, []);

  return (
    <VStack w="100%" alignItems={"center"} align="stretch">
      {tokens?.length &&
        tokens.map((item: any, index: number) => (
          <NavLink key={index}>
            <HStack justifyContent="space-between">
              <Box w={"50px"}>
                <Image src={item.logoURL} alt="" />
              </Box>
              <Box w={"222px"}>
                <Text fontSize="sm">{item.tokenName}</Text>
                <Text fontSize="sm">{item.tokenSymbol}</Text>
              </Box>

              <Button
                // onClick={handleOpenModal}
                // bg={colorMode === "dark" ? "gray.800" : "gray.300"}
                minW="140px"
                border="1px"
                borderColor={colorMode === "dark" ? "gray.700" : "gray.300"}
                _hover={{
                  border: "1px",
                  borderStyle: "solid",
                  borderColor: "blue.400",
                  backgroundColor:
                    colorMode === "dark" ? "gray.700" : "blue.200",
                }}
                borderRadius="md"
                m="1px"
                px={3}
                height="38px"
                onClick={() => {
                  setCopied(!copied);
                  navigator.clipboard.writeText(item.tokenAddress);
                }}
              >
                <Text fontSize="md" fontWeight="medium" p="2">
                  {item.tokenAddress &&
                    `${item.tokenAddress.slice(
                      0,
                      6
                    )}...${item.tokenAddress.slice(
                      item.tokenAddress.length - 4,
                      item.tokenAddress.length
                    )}`}
                </Text>

                {item.tokenAddress && (
                  <CopyIcon m={2} color={copied ? "blue.800" : "gray.400"} />
                )}
                {item.tokenAddress && <Identicon account={item.tokenAddress} />}
              </Button>
            </HStack>
          </NavLink>
        ))}
      {/* <Box p={"40px"}>
        <Button
          h={"40px"}
          border="1px"
          borderStyle="solid"
          borderColor="gray.400"
          isLoading={loading}
          loadingText="Loading..."
          onClick={getTokens}
        >
          Get Tokens
        </Button>
      </Box> */}
    </VStack>
  );
}
