import { useEffect } from "react";
import { Box, Flex, VStack, Image } from "@chakra-ui/react";
import { useLocation } from "react-router-dom";
import { Hero } from "./Hero";
import { Logo } from "./Logo";
import logo from "../assets/metamask-fox.svg";
import useWindowDimensions from "../hooks/useWindowDimensions";

const useScrollToTop = () => {
  const location = useLocation();
  useEffect(() => {
    window.scrollTo({ top: 0 });
  }, [location]);
};

export default function Contact() {
  useScrollToTop();
  const { height, width } = useWindowDimensions();
  return (
    <Flex align="center" justify="center" padding="5px" minH="85vh">
      <Box
        mt={"80px"}
        // h="85%"
        // w="85%"
        minW={width > 600 ? "80%" : "95%"}
        minH="70vh"
        alignSelf="center"
        border="1px"
        borderStyle="solid"
        borderColor="gray.600"
        borderRadius="3xl"
        padding={width > 600 ? "50px" : "20px"}
        //bgGradient="linear(to-t, purple.800, gray.700)"
      >
        <Box
          minH={height / 2}
          minW={width > 600 ? "80%" : "95%"}
          border="1px"
          borderStyle="solid"
          borderColor="gray.600"
          borderRadius="3xl"
        >
          <Box mt={"80px"} h="100%" w="100%" minH="85vh" padding="50px">
            <VStack spacing={8} align="stretch" alignItems="center">
              <Hero title="Chakra NFT Minter" />
              {/* <Logo boxSize="184px" src={logo} /> */}
              <Image src={logo} />
            </VStack>
          </Box>
        </Box>
      </Box>
    </Flex>
  );
}
