import { VStack, Text } from "@chakra-ui/react";
import { useState, useEffect, useRef } from "react";
import { formatEther } from "@ethersproject/units";
import Countdown from "react-countdown";

const STAKER_ABI = require("../assets/newstaker-abi.json");
const STAKER_ADDRESS = process.env.REACT_APP_STAKER_ADDRESS;

export const Stake = (props: any) => {
  const web3 = props.web3;
  const account = props.account;

  var stakerContract = new web3.eth.Contract(STAKER_ABI, STAKER_ADDRESS);

  const [stake, setStake] = useState<any>([]);

  var isMounted = useRef(false);

  useEffect(() => {
    isMounted.current = true;

    stakerContract.methods
      .getUserStake(account)
      .call()
      .then((res: any) => {
        if (isMounted.current) {
          console.log(res);
          setStake(res);
        }
      })
      .catch((e: any) => {
        console.log(e);
      });

    return () => {
      // executed when unmount
      isMounted.current = false;
    };
  }, []);

  //   var blockNumber = web3.eth.blockNumber;
  //   var timestamp = web3.eth.getBlock(blockNumber).timestamp;
  //   var timeLeft = stake?.expiration - timestamp;

  //   var daysLeft = Math.round(
  //     (stake?.expiration * 1000 - new Date().getTime()) / (1000 * 60 * 60 * 24)
  //   );
  //   var hoursLeft = Math.round(
  //     (stake?.expiration * 1000 - new Date().getTime()) / (1000 * 60 * 60)
  //   );
  //   var minutesLeft = Math.round(
  //     (stake?.expiration * 1000 - new Date().getTime()) / (1000 * 60)
  //   );

  //   console.log(new Date(stake?.expiration * 1000), new Date());
  var expirationDate = new Date(stake?.expiration * 1000).toDateString();
  //   const diffInMs = stake?.expiration * 1000 - new Date().getTime();
  //   const diffInDays = diffInMs / (1000 * 60 * 60 * 24);
  //   const diffInHours = diffInMs / (1000 * 60 * 60);
  //   const diffInMinutes = diffInMs / (1000 * 60);
  //   console.log(diffInDays, diffInHours, diffInMinutes);
  var amount = stake?.expiration * 1000 - new Date().getTime() || 0;

  return (
    <VStack w="100%" h="100%" spacing={"20px"} mt={"3px"}>
      <Text fontSize="lg" ml={4}>
        {stake?.amount &&
          "Stake: " + parseFloat(formatEther(stake?.amount)).toFixed(3)}
      </Text>
      <Text fontSize="lg" ml={4}>
        Duration: {stake?.duration} Days
      </Text>

      <Text fontSize="lg" ml={4}>
        Maturity: {expirationDate}
      </Text>
      {/* <Text fontSize="lg" ml={4}>
        Remaining: {daysLeft} days, {hoursLeft} hours, {minutesLeft} minutes
      </Text> */}
      <Text fontSize="lg" ml={4}>
        Remaining:{" "}
        <Countdown
          date={Date.now() + amount}
          //   date={Date.now() + (stake?.expiration * 1000 - new Date().getTime())}
        />
      </Text>
      <Text fontSize="lg" ml={4}>
        APR: {stake?.rate}%
      </Text>
    </VStack>
  );
};
