import { useState } from "react";
import { VStack, StackDivider, Button, Text, Input } from "@chakra-ui/react";

const FACTORY_ADDRESS = process.env.REACT_APP_FACTORY_ADDRESS;
const WHITELIST_ADDRESS = process.env.REACT_APP_WHITELIST_ADDRESS;
const CONTRACT_ABI = require("../assets/vf-abi.json");

export default function VaultFactory(props: any) {
  const web3 = props.web3;
  const account = props.account;
  var factoryContract = new web3.eth.Contract(CONTRACT_ABI, FACTORY_ADDRESS);
  const [result, setResult] = useState<any>();
  const [name, setName] = useState<string>("");
  const [token, setToken] = useState<string>("");
  const [loading, setLoading] = useState<any>(false);
  //console.log(FACTORY_ADDRESS, WHITELIST_ADDRESS);

  async function createVault() {
    setLoading(true);
    const weiValue = web3.utils.toWei("0.2", "ether");
    console.log(weiValue);
    let tx = await factoryContract.methods
      .createVault(
        token,
        WHITELIST_ADDRESS,
        "0x0b8D384b63a5e2428F649a43fE8B93627BE45cC4", //apiKey
        name
      )
      .send({
        from: account,
        value: weiValue,
        gas: "940000",
        gasLimit: "8000000000",
      });

    /**
     * tx.hash is transaction id that we can use to create etherscan link
     */
    if (tx?.transactionHash) {
      console.log(tx.transactionHash);
      // console.log(tx);
      console.log(
        "Vault address",
        tx.events.VaultDeployed.returnValues.vaultAddress
      );
      setResult(
        `https://${props.network}.etherscan.io/tx/${tx.transactionHash}`
      );
      props.updateBalance();
    }
    setLoading(false);
  }

  return (
    <VStack
      w="100%"
      alignItems={"center"}
      verticalAlign={"center"}
      spacing={"25px"}
      //divider={<StackDivider h={"50px"} />}
    >
      <StackDivider h={"50px"} />
      <Text fontSize="sm" ml={4}>
        Choose a name for your vault for reference
      </Text>
      <Input
        w={"80%"}
        border="1px"
        borderStyle="solid"
        borderColor="blue.300"
        style={{ textAlign: "center" }}
        placeholder={"Vault Name"}
        value={name}
        maxW={"3xl"}
        onChange={(e) => {
          setName(e.target.value);
        }}
      />
      <Input
        w={"80%"}
        border="1px"
        borderStyle="solid"
        borderColor="blue.300"
        style={{ textAlign: "center" }}
        placeholder={"ERC20 Token Address"}
        value={token}
        maxW={"3xl"}
        onChange={(e) => {
          setToken(e.target.value);
        }}
      />
      <StackDivider h={"120px"} />
      <Button
        h={"40px"}
        minW={"100%"}
        border="1px"
        borderStyle="solid"
        borderColor="gray.400"
        isLoading={loading}
        loadingText="Loading..."
        onClick={createVault}
      >
        Create Vault
      </Button>
    </VStack>
  );
}
